.header {
  font-size: 22px;
  font-family: "Poiret One";
  color: #69c9ca;
  text-align: center;
  text-transform: uppercase;
  margin-top: 50px;
  margin-bottom: 30px;
  font-weight: 300;
}
.form {
  width: 320px;
  margin: 0 auto;
}
.form input {
  border: 1px solid #fff;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  padding: 15px 20px;
  background: none;
  user-select: initial;
  width: 100%;
}
.input {
  margin-bottom: 20px;
}
.form .submit {
  border: 1px solid #69c9ca;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  transition: all 0.3s ease;
  border-radius: 0;
  padding: 15px 0;
  width: 100%;
  position: relative;
  background: #69C9CA;
  cursor: pointer;
  font-family: 'Open Sans';
}
.statusTextErr, .statusTextSuccess {
  position: absolute;
  left: 0;
  bottom: -62px;
  font-size: 11px;
  text-align: left;
  display: flex;
  align-items: center;
  line-height: 1.5;
  width: 100%;
  text-transform: none;
  padding: 5px 10px;
  white-space: pre-line;
}
.statusTextSuccess {
  color: #6AC259;
  border: 1px solid #6AC259;
}
.statusTextErr {
  color: #8B2C2C;
  border: 1px solid #8B2C2C;
}
.statusTextErr::before, .statusTextErr::after {
  content: '';
  position: absolute;
  left: 20px; top: -20px;
  border: 10px solid transparent;
  border-bottom: 10px solid #8B2C2C;
 }
 .statusTextErr::after {
  border-bottom: 10px solid #222222;
  top: -19px;
 }
 .statusTextSuccess::before, .statusTextSuccess::after {
  content: '';
  position: absolute;
  left: 20px; top: -20px;
  border: 10px solid transparent;
  border-bottom: 10px solid #6AC259;
 }
 .statusTextSuccess::after {
  border-bottom: 10px solid #222;
  top: -19px;
 }
.icon {
  display: block;
  width: auto;
  height: 40px;
  margin-right: 5px;
}
.submit:hover, .submit:active, .submit:focus {
  background: #69c9ca;
  color: #fff;
  border-radius: 0;
  padding: 15px 0;
}
.statusText {
  color: #fff;
}

@media(min-width: 1200px) {
  .header {
    font-size: 24px;
    margin-top: 0;
    margin-bottom: 27px;
    text-align: left;
  }
  .form {
    width: 100%;
  }
  .form .input {
    margin-bottom: 18px;
    width: 275px;
  }
  .form .submit {
    width: 200px;
    padding: 12px 0;
    background: none;
    color: #69C9CA;
    font-size: 18px;
    line-height: 30px;
    border: 2px solid #69C9CA;
  }
  .submit:hover, .submit:active, .submit:focus {
    padding: 12px 0;
    background: #69C9CA;
    color: #FFFFFF;
  }
  .statusTextErr, .statusTextSuccess {
    width: 360px;
    padding: 10px 20px;
    font-size: 14px;
    bottom: -75px;
    left: 0;
  }
}
