.list {
  padding: 0;
  list-style: none;
}

.contacts {
  font-size: 14px;
  font-weight: 300;
  text-align: center;
  color: #fff;
}

.contacts p {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contacts p:not(:last-child) {
  margin-bottom: 15px;
}

.contacts p a {
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}

.contacts p a:hover {
  text-decoration: none;
  color: #fff;
}

.contacts i {
  color: #69c9ca;
  margin-right: 10px;
}

@media(min-width: 1200px) {
  .list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 15px;
    padding: 53px 100px 44px 100px;

  }

  .linkLogo {
    display: inline-block;
    margin-bottom: 40px;
  }

  .logo {
    display: block;
    height: auto;
    width: 113px;
  }

  .contacts p {
    justify-content: flex-start;
  }

  .contacts p:not(:last-child) {
    margin-bottom: 13px;
  }

  .contacts i {
    margin-right: 0.75vw;
  }

  .listItemServices {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .listItemServicesHeader {
    font-size: 24px;
    font-weight: 300;
    color: #69c9ca;
    line-height: 1;
    margin: 0 0 27px 0;
    text-transform: uppercase;
  }
  .navs {
    padding: 0;
    list-style: none;
  }

  .listItem {
    font-weight: 300;
    font-size: 16px;
    line-height: 36px;
    color: #FFFFFF;
  }
  .link {
    text-decoration: none;
    color: inherit;
  }
}
