.section {
  width: 320px;
  margin: 0 auto;
  border-top: 1px solid #69C9CA;
}
.wrapper {
  margin-bottom: 65px;
}
.link {
  display: block;
  margin: 30px auto;
  width: 72px;
}
.logo {
  display: block;
  width: 72px;
  height: auto;
}
.footer {
  width: 250px;
  margin: 0 auto;
  font-weight: 300;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  text-align: center;
}

@media(min-width: 1200px) {
  .section {
    width: 1170px;
  }

  .footer {
    width: auto;
    font-size: 14px;
    line-height: 36px;
  }
}
